.dashiconsDropdown {
  width: auto;
}
.dashiconsDropdown a {
  display: inline-block;
  overflow: hidden;
  text-decoration: none;
  border: 1px solid #8c8f94;
  font-size: 14px;
  line-height: 2;
  color: #2c3338;
  box-shadow: none;
  border-radius: 3px;
  padding: 0 48px 0 8px;
  min-height: 30px;
  max-width: 25rem;
  background: #fff url(data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2220%22%20height%3D%2220%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20d%3D%22M5%206l5%205%205-5%202%201-7%207-7-7%202-1z%22%20fill%3D%22%23555%22%2F%3E%3C%2Fsvg%3E) no-repeat right 5px top 55%;
  background-size: 16px 16px;
  cursor: pointer;
  vertical-align: middle;
}
.dashiconsDropdown a span {
  vertical-align: inherit;
}
.dashiconsDropdown a.open {
  color: #000;
}
.dashiconsDropdown a.open + ul {
  display: block;
}
.dashiconsDropdown ul {
  list-style-type: none;
  padding: 5px;
  margin: 0;
  display: none;
  border: 1px solid #000;
}
.dashiconsDropdown ul li {
  display: inline-block;
}
.dashiconsDropdown ul li input {
  display: none;
}
.dashiconsDropdown ul li label {
  display: inline-block;
  padding: 4px;
  cursor: pointer;
  color: #777;
  border: 1px solid transparent;
}
.dashiconsDropdown ul li label:hover {
  color: #000;
}
.dashiconsDropdown ul li :checked + label {
  color: #000;
  border-color: #000;
}
.dashiconsDropdown .dashiconsDropdown_removeButton {
  width: 100%;
}
.dashiconsDropdown .dashiconsDropdown_removeButton button {
  display: block;
  font-size: 13px;
  border: none;
  padding: 5px;
  background: transparent;
  color: #b32d2e;
}
.dashiconsDropdown .dashiconsDropdown_removeButton button:hover {
  cursor: pointer;
  text-decoration: underline;
}

.visuallyhidden {
  display: none;
}

section {
  margin-top: 2em;
}

table {
  border-collapse: collapse;
}
table td {
  padding: 0.1em 0;
}
table td:nth-child(1n+2) {
  padding-left: 2em;
}

.dashTiles {
  display: grid;
  grid-auto-rows: 10em;
  grid-auto-columns: 10em;
  grid-auto-flow: column;
  grid-gap: 10px;
  margin-top: 2em;
}
.dashTiles a {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.15s;
  border-radius: 5px;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.2);
  background-color: var(--lw_white);
  padding: 10px;
  text-align: center;
  text-decoration: none;
  color: var(--lw_grey-dark);
  font-size: 120%;
  font-weight: 500;
}
.dashTiles a:hover {
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.5333333333);
  text-decoration: underline;
  color: var(--lw_grey-darkest);
}